const hasOwnProperty = function (obj, key) {
    return Object.prototype.hasOwnProperty.call(obj, key)
}

//  Set 
class D_Set {
    constructor() {
        this.value = {}; // value 
        this.n = 0; // n 


        // arguments 
        //  new D_Set(1,'sarah',{})  add 
        this.add.apply(this, arguments);
    }
    //  D_Set  _v2s
    // 
    static _v2s(val) {
        switch (val) {
            case undefined: return 'u'; //  undefined 'u'
            case null: return 'n';
            case true: return 't';
            case false: return 'f';
            default: switch (typeof val) {
                case 'number': return '#' + val; //  #  , #123, #0.5
                case 'string': return '"' + val; //  "  ,  "hello, "world
                default: return '@' + objectId(val); //  @ objectId @100
            }
        }

        function objectId(o) {
            var prop = "|**objectid**|"; // / /  id
            if (!hasOwnProperty(o, prop)) { // 
                o[prop] = D_Set._v2s.next++; //  next
            }
            return o[prop];
        }
    }
    get() {
        return this.value;
    }
    set() {
        this.clear();
        this.add(arguments);
    }
    notify() {
        var ob = this.__ob__;
        if (ob) {
            // notify change
            ob.dep.notify();
        }
    }
    // 
    add() {
        // 
        for (var i = 0; i < arguments.length; i++) {
            var val = arguments[i], // 
                str = D_Set._v2s(val); // 

            // 
            if (!hasOwnProperty(this.value, str)) {
                this.value[str] = val;
                this.n++; // 
                this.notify();
            }
        }
        return this; // 
    }
    // 
    delete() {
        // 
        for (var i = 0; i < arguments.length; i++) {
            var str = D_Set._v2s(arguments[i]); // 
            if (hasOwnProperty(this.value, str)) { //  value 
                delete this.value[str]; // 
                this.n--; // 
                this.notify();
            }
        }
        return this; // 
    }
    // 
    has(value) {
        return hasOwnProperty(this.value, D_Set._v2s(value));
    }
    // 
    size() {
        return this.n;
    }
    //  f
    forEach(f, context) {
        for (var s in this.value) {
            if (hasOwnProperty(this.value, s)) {
                f.call(context, this.value[s]);
            }
        }
    }
    // 
    clear() {
        this.value = {};
        this.n = 0;
        this.notify();
    }
}







//  D_Set._v2s.next  next 100
// 
D_Set._v2s.next = 100;

export const _Set = D_Set